/*Arrow down styling */
.arrow-down{
  /* position: fixed;
  text-align: center;
  top: 750px;
  left: 50%;
  transform: translateX(-50%); */
  width: 100%;
  text-align: center;
  -webkit-animation: bounce 1s alternate cubic-bezier(.5,.05,1,.5) infinite;
          animation: bounce 1s alternate cubic-bezier(.5,.05,1,.5) infinite;
}

/*Animation for the arrow down*/
@-webkit-keyframes bounce{
  0%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100%{
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}
@keyframes bounce{
  0%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100%{
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}

