/*Styling for the buttons*/
.btn{
  position: relative;
  font-family: Avenir;
  display: inline-block;
  padding: 20px;
  color: #0595FF;
  background: transparent;
  border: 3px solid #0595FF;
  border-radius: 50px;
  transition: all 0.4s ease-in-out;
  z-index: 1;
  overflow: hidden;
}

.btn:before{
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 0%;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 0% 10% 10% 0;
  background-image: linear-gradient(45deg, #0595FF, #0595FF);
  transition: 0.4s;
  z-index: -1;
}
